.HTMLEmbla {
  overflow: hidden;
  width: 100%;
 
  backdrop-filter: brightness(0.8);
  min-height: 65vh;
  max-height: 65vh; 
  border-radius: 50px 50px 50px 50px;;
}

.HTMLEmbla__viewport {
  overflow: hidden;
  width: 100%;
}

.HTMLEmbla__container {
  display: flex;
  flex-direction: row; /* make it horizontal */
  width: 100%;
}

.HTMLEmbla__slide {
  flex: 0 0 100%; /* each slide takes full width */
  min-width: 100%;
  display: flex;
  justify-content:left;
  align-items:center;
  padding: 2rem;
  box-sizing: border-box;
  text-align: left;
}

.slide-inner {
  max-width: 800px;
}

.orangetext {
  color: #f26522;
}

.HTMLEmbla__controls {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.HTMLEmbla__dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 0 5px;
  border: none;
  background-color: #ccc;
  cursor: pointer;
}

.HTMLEmbla__dot--selected {
  background-color: #f26522;
}

.slide-inner h2{
  color: #f26522;
  font-size:3rem;
  font-weight: 1000;
}

.slide-inner p{
  font-size: 1.5rem;
  font-weight: 600;
}