
.subscription-form input{
    width:200px;
    border-radius: 20rem;
    border-color: black;
    padding: 1rem;
    border-left: 2rem 2rem solid;
}

.subscription-form{
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    margin-left: 4rem;
}

.form-description{
    font-family: "Oswald", sans-serif;
    font-size: 1.2rem;
    padding-left: 2vw;
    text-transform: uppercase;
    font-weight: 600;
}

.inputfeed{
    display: flex;
    flex-direction: column;
}

.inputfeed button{
    
    margin-top: 1rem;
    color: white;
    background-color: black;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 12rem;
    height:3rem;
    width:9rem;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.inputfeed button:hover{
    background-color: #f26522;
    color: white;
    transform: translateY(-0.45vh);
    box-shadow: 0 12px 12px rgba(0, 0, 0, 0.2);
    
}