.carousel1{
    margin:0;
    font-family: "Oswald", sans-serif;
    font-size: 2.5rem;
    color: white;
    margin-bottom: 50px;
    text-align: center;
    height:60vh;
    padding-top: 5vh;
    
    
    font-style: italic;

}
.home{
  width: 100%;
}
.carousel1 p{
    text-align: center;
    text-rendering: geometricPrecision;
    color: black;
    font-style: normal;
  }

.carousel1 h2{
    margin: 0;
}
.carousel1 h1{
    margin: 0;
}

.buttonAbout{
  display:flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
}
.orangetext{
    color: #f26522;
    text-shadow: 2.5px 5px 10px#3c3c3c;
}
a{
  text-decoration: none;
  font-style: none
}
.scrollabout{
    color: white;
    background-color: #f26522;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    border-radius: 12rem;
    height:2.7rem;
    width:9rem;
    transition: transform 0.2s ease;
    text-decoration: none;
    align-items: center;
  }

.scrollabout:hover{
    transform: translateY(-2px);
    box-shadow: 2px 2px 10px #3c3c3c;

}
.subscriptionSec{
  align-items: center;
  justify-content: center;
  display: flex;
}
.subsForm{
    background-color: white;
    padding: 2rem 2rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 5vh;
    gap:10px;
    width:70vw;
}
.embla {
    max-width: 100%;
    margin: 0;
    padding: 5vh;
    
    --slide-height: 75vh;
    --slide-spacing: 1rem;
    --slide-size: 35%;
  }
  .embla__viewport {
    overflow: hidden;
  }
  .embla__container {
    display: flex;
    touch-action: pan-y pinch-zoom;
    margin-left: calc(var(--slide-spacing) * -1);
    
  }
  .embla__slide {
    transform: translate3d(0, 0, 0);
    flex: 0 0 var(--slide-size);
    min-width: 0;
    padding-left: var(--slide-spacing);
}
  .embla__slide__number {
    box-shadow: inset 0 0 0 0.2rem var(--detail-medium-contrast);
    border-radius: 1.8rem;
    font-size: 4rem;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    height: var(--slide-height);
    user-select: none;
  }
  .Homeimages {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures image fills slide without distortion */
    object-position: center;
    display: block;
  }

  .home{
    background: linear-gradient(#f26522,white ,white,white,white,white,white,white);
  }
  
  .scroller{
    background-color: #f26522;
    margin-top: 10px;
  }
  .scroller-inner{
    display: inline-flex;
    gap:2rem;
    animation: scroll 15s linear infinite;
  }

  .scroller-item{
    font-size: 1rem;
    color: white;
    text-transform: uppercase;
    display:flex;
    align-items:center;
    gap:5rem; 
  }

  .subsDescription{
    font-family: "Oswald", sans-serif;
    flex: 1 1 450px;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }

  .subsDescription h2{
    font-style: italic;
    font-size: 2rem;
  }

  .subsDescription p{
    font-size:1.5rem;
  }
  .subsinput{
    display: flex;
    align-items: center;
    justify-content: left;
    flex: 1 1 200px;
  }

  .about{
    background-color: rgb(0,0,0,0);
    height: 40vh;
    padding-right: 2rem;
    display:flex;
    font-family:  "Oswald", sans-serif;
    color: white;
    font-size: 1.3rem;
    justify-content: space-between;
    text-align: end;
    gap:3rem
  }
  .rightcol{
    width:60vw;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: center;
    text-align: end;
  }
  .leftcol{
    width:30vw;
    background-color: #3c3c3c;
    height:100%;
  }
  .leftcol img{
    height:40vh;
    border-radius: 0rem 5rem 5rem 0rem ;
  }
  .heading{
    color:#f26522;
    text-align: end;
    
  }
  .containparalax{
    min-height:65vh;
    max-height:65vh;
  }
  .parallax {
    /* The image used */
    background-image: url("../assets/paralaxA.png");
    /* Set a specific height */
    min-height: 65vh;
    max-height: 65vh; 
    border-radius: 50px 50px 50px 50px;
    color: aliceblue;
    font-family: "Oswald", sans-serif;
    margin-left: 2rem;
    margin-right: 2rem;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: repeat;
    background-size:cover;
    display: flex;
    justify-content:left;
    align-items: center;
  }
 
  .contactDetails a{
    color:white;
  }
  .know-us-more{
    margin-top:10vh;
    display: flex;
    flex-wrap: wrap;
    margin-left: 0;
    margin-right: 0;
    justify-content: center;
    align-items: center;
    gap:3rem;

    background-color: white;
    padding-top: 5rem;
    padding-bottom: 5rem;

  }

  .letsConnect, .ourSocials{
    width:25rem;
    height:20rem;
    background-color: #3c3c3c;
    padding:2rem;
    color: white;
    font-family: "Oswald", sans-serif;
    border-radius: 50px;
    transition: transform 0.5s ease;
  }

  .letsConnect:hover{
    transform:translateY(-3px);
    box-shadow: -5px 5px 20px#f26522;
  }
  .ourSocials:hover{
    transform:translateY(-3px);
    box-shadow: -5px 5px 20px#f26522;
  }
  .letsConnect p, .ourSocials p{
    font-size:1.3rem
  }

  .letsConnect h1, .ourSocials h1{
    color: #f26522;
  }

  .location, .calldetail, .email, .instagram, .linkedin, .facebook{
    letter-spacing: 1px;
    font-size: 0.9rem;
    display: flex;
    margin-bottom:3px;
    cursor:pointer;
  }

  .logo{
    font-size:1.2rem;
    margin-right:15px
  }


  
  .register{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top:5vh;
    background-color: #3c3c3c;
  }

  .registerDetails{
    flex: 1 1 200px;
    
  }

  .registerForm{
    flex: 1 1 400px;
  }


  @keyframes scroll {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-100%);
    }
  }